.App {
  text-align: center;
}

.App-logo {
  height: 3vmin;
  pointer-events: none;
  margin: 0;

}

.cotainer{  
  display: inline-flex;
    max-width: "100%";
    align-items: "center";
    justify-content: "center";
    flex-direction: column;
}

.App-profile {
  height: 35vh;
  pointer-events: none;
  margin: 0;
  border-radius: 50%;
  border: 2vh solid;
  margin: 20px 20px;
  border-color: rgba(255, 255, 255, 0.212);

  box-shadow: 5px 5px rgba(25, 50, 54, 0.7);
}

.App-Logo-PK {
  height: 35vh;
  pointer-events: none;
  border-radius: 50%;
  border: 2vh solid;
  margin: 20px 20px;
  background-color: rgba(255, 255, 255, 0.712);
  border-color: rgba(255, 255, 255, 0.212);
  box-shadow: 5px 5px rgba(107, 24, 72, 0.7);
}

.app_bar{
  background-color:  rgba(23, 103, 117, 0.7);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.paper{
  display: inline-flex;
  text-align: "center";
  margin: "auto";
  align-items: "center";
  justify-content: "center";
  flex-direction: column;
}

.paper2{
  display: inline-flex;
  text-align: "center";
  margin: "auto";
  align-items: "center";
  justify-content: "center";
  flex-direction: column;
}

.App-header {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color:  rgba(29, 131, 131, 0.8);
}

.App-header2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color:  rgba(255, 255, 255, 1.0);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
